  .card__image {
    object-position: 50%;
  }
}

.card:hover .card__image {
  display: none;
}

.card:hover .card__title {
  position: static;
  order: -1;
}

.card:hover {
  padding-left: 25px;
}

.card:focus .card__title {
  position: static;
  order: -1;
}

.card:focus .card__image {
  display: none;
}

.card:focus {
  padding-left: 25px;
}

.card__button:focus ~ picture > .card__image {
  display: none;
}

.card__button:focus ~ .card__title {
  position: static;
  order: -1;
}